.form{
    color:black;
    border-radius: 6px;
    border-top:55px;
    background-color: #f2f2f2;
    padding: 20px;
    max-width:30%;
    margin-left:auto;
    margin-right:auto;
    margin-top: 50px;
}
.form li{
    list-style:none;
    width:50%;
}

.form input  {
	display:block;
	
	
  width: 100%; 
  padding: 5px; 
  border: 1px solid #ccc; 
  border-radius: 4px; 
  box-sizing: border-box;
  margin-top: 6px; 
  margin-bottom: 16px; 
  resize: vertical 
}
.form h2{
    position: relative;
    text-align: center;
}
